// React core
import React from 'react'
import PropTypes from 'prop-types'
import { compose } from 'recompose'

// Styling
import classNames from 'classnames'
import withWidth, { isWidthDown } from '@material-ui/core/withWidth'
import { withTheme } from '@material-ui/core/styles'
import { withStyles } from '@material-ui/core/styles'
import styles from './styles'

// Components
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import InputBase from '@material-ui/core/InputBase'
import Chip from '@material-ui/core/Chip'
import Badge from '@material-ui/core/Badge'
import Avatar from '@material-ui/core/Avatar'
import Drawer from '@material-ui/core/Drawer'
import Divider from '@material-ui/core/Divider'
import Navigation from './Navigation'
import Typography from '@material-ui/core/Typography'


// Icons
import MenuIcon from '@material-ui/icons/Menu'
import SearchIcon from '@material-ui/icons/Search'
import NotificationsIcon from '@material-ui/icons/Notifications'
import ChangeColorIcon from '@material-ui/icons/InvertColors'
import HighlightIcon from '@material-ui/icons/Highlight'
import FormatTextdirectionRToLIcon from '@material-ui/icons/FormatTextdirectionRToL'
import CallMadeicon from '@material-ui/icons/CallMade'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import ChevronRightIcon from '@material-ui/icons/ChevronRight'
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft'

// Media
import ciapd from '../../media/ciapd.png'
import { Hidden } from '@material-ui/core';

class Navbar extends React.Component {
  static propTypes = {
    width: PropTypes.string.isRequired,
    theme: PropTypes.object.isRequired,
    classes: PropTypes.object.isRequired
  }

  state = {
    leftBarOpened: false,
    rightBarOpened: false
  }

  toggleLeftBarState = () => {
    this.setState(({ leftBarOpened: prevLeftBarOpened }) => ({
      leftBarOpened: !prevLeftBarOpened
    }))
  }

  toggleRightBarState = () => {
    this.setState(({ rightBarOpened: prevRightBarOpened }) => ({
      rightBarOpened: !prevRightBarOpened
    }))
  }

  render () {
    const { classes, theme } = this.props
    return (
      <div className={classes.root}>
        <AppBar
          position='fixed'
          style={{
            paddingRight: (
              isWidthDown('md', this.props.width) ? 12 :
                this.state.leftBarOpened ? 0 : 24
            )
          }}
          className={classNames(classes.appBar, {
            [classes.appBarShift]: this.state.leftBarOpened
          })}
        >
          <Toolbar disableGutters={!this.state.leftBarOpened}>
            <IconButton
              color='inherit'
              aria-label='Open drawer'
              onClick={this.toggleLeftBarState}
              className={classNames(classes.menuButton, {
                [classes.hide]: this.state.leftBarOpened || this.state.rightBarOpened
              })}
            >
              <MenuIcon />
            </IconButton>
            <img className={classes.ciapdLogo} src={ciapd} alt='CIAPD' />
            <div className={classes.grow} />
            <Hidden mdDown>
              <div className={classes.search}>
                <div className={classes.searchIcon}>
                  <SearchIcon />
                </div>
                <InputBase
                  placeholder='Search…'
                  classes={{
                    root: classes.inputRoot,
                    input: classes.inputInput
                  }}
                />
              </div>
              <div className={classes.topbarMenu}>
                <Chip
                  avatar={<Avatar alt='Manuela' className={classes.chipImage} src='https://material-ui.com/static/images/uxceo-128.jpg' />}
                  label='Manuela Costa'
                  className={classes.chipEl}
                />
                <IconButton color="inherit">
                  <Badge badgeContent={17} color="error">
                    <NotificationsIcon />
                  </Badge>
                </IconButton>
                <IconButton color="inherit">
                  <ChangeColorIcon />
                </IconButton>
                <IconButton color="inherit">
                  <HighlightIcon />
                </IconButton>
                <IconButton color="inherit">
                  <FormatTextdirectionRToLIcon />
                </IconButton>
                <IconButton color="inherit">
                  <CallMadeicon />
                </IconButton>
              </div>
            </Hidden>
            <Hidden lgUp>
              <IconButton
                color='inherit'
                aria-label='Open menu'
                onClick={this.toggleRightBarState}
                className={classNames(classes.any, {
                  [classes.hide]: this.state.leftBarOpened || this.state.rightBarOpened
                })}
              >
                <MoreVertIcon />
              </IconButton>
            </Hidden>
          </Toolbar>
        </AppBar>
        <Drawer
          variant='permanent'
          classes={{
            paper: classNames(classes.drawerPaper, !this.state.leftBarOpened && classes.drawerPaperClose)
          }}
          open={this.state.leftBarOpened}
        >
          <div className={classes.toolbar}>
            <IconButton onClick={this.toggleLeftBarState}>
              {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
            </IconButton>
          </div>
          <Divider />
          <Navigation />
        </Drawer>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          <Typography paragraph>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor
            incididunt ut labore et dolore magna aliqua. Rhoncus dolor purus non enim praesent
            elementum facilisis leo vel. Risus at ultrices mi tempus imperdiet. Semper risus in
            hendrerit gravida rutrum quisque non tellus. Convallis convallis tellus id interdum
            velit laoreet id donec ultrices. Odio morbi quis commodo odio aenean sed adipiscing.
            Amet nisl suscipit adipiscing bibendum est ultricies integer quis. Cursus euismod quis
            viverra nibh cras. Metus vulputate eu scelerisque felis imperdiet proin fermentum leo.
            Mauris commodo quis imperdiet massa tincidunt. Cras tincidunt lobortis feugiat vivamus
            at augue. At augue eget arcu dictum varius duis at consectetur lorem. Velit sed
            ullamcorper morbi tincidunt. Lorem donec massa sapien faucibus et molestie ac.
          </Typography>
          <Typography paragraph>
            Consequat mauris nunc congue nisi vitae suscipit. Fringilla est ullamcorper eget nulla
            facilisi etiam dignissim diam. Pulvinar elementum integer enim neque volutpat ac
            tincidunt. Ornare suspendisse sed nisi lacus sed viverra tellus. Purus sit amet volutpat
            consequat mauris. Elementum eu facilisis sed odio morbi. Euismod lacinia at quis risus
            sed vulputate odio. Morbi tincidunt ornare massa eget egestas purus viverra accumsan in.
            In hendrerit gravida rutrum quisque non tellus orci ac. Pellentesque nec nam aliquam sem
            et tortor. Habitant morbi tristique senectus et. Adipiscing elit duis tristique
            sollicitudin nibh sit. Ornare aenean euismod elementum nisi quis eleifend. Commodo
            viverra maecenas accumsan lacus vel facilisis. Nulla posuere sollicitudin aliquam
            ultrices sagittis orci a.
          </Typography>
        </main>
      </div>
    )
  }
}

export default compose(
  withWidth(),
  withTheme(),
  withStyles(styles)
)(Navbar);
